@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Book.ttf);
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Heavy.ttf);
  font-style: normal;
  font-weight: 900;
}

* {
  box-sizing: border-box;
}

html {
  font-family: Avenir, sans-serif;
}

.form__search {
  margin-top: 20px;
}

.input {
  width: 100%;
  font-family: inherit;
  background: no-repeat url(./images/Search.svg);
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
  color: #3d4e61;
  outline: none;
}

.input:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.input:focus {
  background: no-repeat url(./images/Search.svg) linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  font-weight: 900;
}

.input__big {
  height: 70px;
  font-size: 16px;
  line-height: 22px;
  background-position: 26px 25px;
  background-size: 19px 19px;
  padding-left: 62px;
}

.input__small {
  height: 42px;
  font-size: 14px;
  line-height: 19px;
  background-position: 12px 14px;
  background-size: 11px 11px;
  padding-left: 33px;
}
